* {
  font-family: sans-serif;
}

.header {
  display: flex;
  flex-direction: column;
  width: 300px;
  text-align: center;
  margin-inline: auto ;
}
.grid-area {
  margin-top: 30px;
  table {
    max-width: 700px;
    margin: auto;
    user-select: none;
    td {
      width: 20px;
      height: 20px;
      &.selected {
        color: rgb(241, 176, 45);
      }
    }
  }
 
}